.Jumbotron{
  position: relative;
  width: 100vw;
  height: 35vh;
  background-image: url('https://online.codingapple.com/wp-content/uploads/2020/05/background.jpg');
  background-size: cover;
  text-align: center;
  color: white;
  min-height: 400px;
}

.Jumbotron h2{
  padding-top: 50px;
  padding-bottom: 20px;
  font-size: 70px;
  font-weight: 500;
}

.Jumbotron p{
  font-size: 20px;
}

.Jumbotron button{
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  margin-top: 60px;
}

.container{
  text-align: center;
}

.container h4{
  font-size: 25px;
  font-weight: 500;
  padding: 5px;
}

.container p{
  font-size: 20px;
  font-weight: 400;
  padding: 5px;
}